/* Banner */
.banner {
  position: absolute;
  top: 50%;
  left:50%;
  transform: translate(-50%,-50%);
  display: inline-block;
  background: rgba(0, 0, 0, 0.3);
  color: var(--mainWhite);
  padding: 2rem 5rem;
  text-align: center;
  letter-spacing: var(--mainSpacing);
  max-width: fit-content;
}
.banner h1 {
  font-size: 1.75rem;
  white-space: nowrap;
}

.banner div {
  width: 16.8rem;
  height: 2px;
  background: var(--primaryColor);
  margin: 1.7rem auto;
}
.banner p {
  text-justify: distribute;
  font-size: 1.2rem;
  margin-bottom: 2rem;
}
@media screen and (max-width: 576px) {

  .banner {
    padding: 0.5rem 0.5rem;
  }

  .banner h1 {
    font-size: 1.4rem;
  }

}
@media screen and (min-width: 992px) {

  .banner {
    padding: 4rem 10rem;
  }

  .banner h1 {
    font-size: 3rem;
  }

}
/* End of Banner */
/* Title */
.section-title {
  text-align: center;
  margin-bottom: 4rem;
}

.section-title h4 {
  font-size: 2rem;
  letter-spacing: var(--mainSpacing);
  /* text-transform: capitalize; */
