/* Notification */

.notification-wrapper{
  position: absolute;
  top: 9rem;
  right: 4rem;
  max-width: 40%;
  max-height: fit-content;
  z-index: 1111;

}

.notification-item{
  box-shadow: var(--lightShadow);
  border-radius: 5px;
  border: .05rem solid rgba(0, 0, 0, 0.5);
  overflow: hidden;
  background-color: rgba(0,0,0,0.2);
  color: rgb(255, 255, 255);
  margin-bottom: 1rem;
  animation: SlideLeft 0.4s;
  animation-fill-mode: forwards;
  /* white-space: nowrap; */
  /* max-width: 20rem; */
}

.notification-item.exit{
  animation: SlideRight 0.4s;
  animation-fill-mode: forwards;
}

@keyframes SlideLeft{
  0% {
    margin-left: 150%;
  }
  100% {
    margin-left: 0;
  }
}

@keyframes SlideRight{
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: 150%;
  }
}


.notification-item p {
  margin: 0;
  padding: .5rem;
  overflow: hidden;
  text-align: justify;
}

.notification-item .bar{
  height: .2rem;
  transition: var(--transition);
}

.notification-item.success .bar{
  background-color: rgb(36, 139, 10);
}

.notification-item.err .bar{
  background-color: rgb(202, 60, 17);
}

.notification-item.warning .bar{
  background-color: rgb(202, 161, 46);
}
.notification-item.none .bar{
  background-color: rgb(37, 108, 201);
}

