.download-container .badge-container {
  height: 18px;
  width: 18px;
  font-size: 12px;
  text-align: center;
  color: rgb(89, 135, 146);
  position: absolute;
  background: #6cc233;
  display: block;
  border-radius: 50%;
  top: 16px;
  left: 14px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

