/* style for the heart shown on music art  */

svg.songHeart {
  position: absolute;
  top: 10px;
  font-size: 4em;
  color: rgb(223, 83, 137);
  transition: all 0.3s cubic-bezier(0.82, 0.33, 0, 1.46);
}

svg.songHeart.right {
  right: -8px;
  top:-4px;
}

svg.songHeart.left {
  left: -8px;
