/* main media player container style */
.mediaPlayerContainer {
  position: fixed;
  right: 0;
  bottom: 0;
  background: url("./images/defaultBg2.jpg") center/cover no-repeat;
  background-attachment: scroll;
  /* background: rgb(211, 230, 235); */
  width: 100%;
  height: 100%;
  z-index: 1400;
  display: inline-block;
  transition: all 0.3s ease;
  /* overflow: hidden; */
}

.playerMinimized {
  transform: translateY(calc(100% - 106px));
  z-index: 0;
  background: rgb(89, 135, 146);
}

.playerPlaylist {
  transform: translateY(-418px);
}

/* short the title based on space */
.musicArtTitle {
  width: 88vw;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

/* style for the playlist of related videos */

.RelatedVideoContainer {
  background: rgb(54, 86, 94);
  /* overflow: -moz-hidden-unscrollable; */
  height: calc(100% - 200px);
  height: 100%;
  min-height: 470px;
  /* half of the portion will be taken by player */
}



.RelatedVideoContainer ul {
  height: 80%;
  margin-bottom: 2rem;
  overflow-y: scroll;
  overflow-x: hidden;
}


div.RelatedVideoContainer * {
  color: rgb(202, 238, 247);
}

 
div.RelatedVideoContainer *:hover {
  color: rgba(199, 119, 199, 0.9)
}

.playlistHeader {
  padding: 7px 15px;
  position: sticky;
  top: 0;
  z-index: 2;
  background: #4f697a7e;
  box-shadow: #06060640 0px 2px 3px;
}

.searchPopper {
  width: 100%;
  height: 100%;
  background: #4f697a;
  color: floralwhite;
  overflow-y: scroll;
}

.searchPopper ul {
  margin-top: 8rem;
}

.playlistHeader h6 {
  margin-right: 50px;
}

/* hide recaptcha badge */
div.grecaptcha-badge {
  display: none;
}

.dayNightToggleBtn {
  height: 25px;
  /* position: absolute; */
  top: 0;
  left: 0;
  color: aquamarine;
}

/* forcing style for bottom tab menu */
div a.MuiTab-root {
  padding: 0;
  line-height: .8;
  min-height: 48px;
  font-weight: normal;
  text-transform: capitalize;
}


/* fix on small screen */
