/* Wheel */

.main-birth {

  margin: 0 auto;
  min-height: calc(100vh - 58px);
  /* display: flex; */
  background-color: var(--clr-grey-9);
  background-image: linear-gradient(to bottom, rgba(0,0,0,0), #2499b6e0);
  overflow-y: auto;
  overflow-x: hidden;
  padding-bottom: 1rem;
  color: hsl(209, 61%, 16%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-family: "Satisfy";
}

.header-birth {
  margin-top: 1rem;
  height: 240px;
}

.header-birth .avt-birth {
  position: absolute;
  left: 1rem;
  top: 7rem;
  cursor: pointer;
}

.header-birth > h2 {
  position: absolute;
  top: 16.5rem;
  right: 5rem;
}

.header-birth > img {
  height: 240px;
  width: 100%;
}

.header-birth .avt-birth > img {
  position: absolute;
  border-radius: 50%;
  height: 340px;
  width: 340px;
  border: .2rem solid rgba(195, 104, 223, 0.9);
  transition: var(--transition);
}



.body-birth {
  border-top: .2rem solid rgba(195, 104, 223, 0.9);
  padding-top: 10rem;
  padding-bottom: 10rem;
  flex: 1;
  text-align: center;
  background: url('https://cdn.pixabay.com/photo/2018/10/26/08/25/red-confetti-3774082_960_720.png') center/cover no-repeat;
}

.body-birth > p {
  position: absolute; 
  right: 1rem;
  top: 20rem;
}


.card-gifts {
  margin: 0 auto;
  width: 70%;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.2);
  border-bottom-left-radius: 5px;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: var(--lightShadow);
  max-height: 40rem;
  overflow-y: auto;
}

